import React, {Component} from 'react';
import './style.css';

/* Redux */
import {bindActionCreators} from "redux";
import {changeCurrentPage} from "../../../Store/Actions/actionMain";
import connect from "react-redux/es/connect/connect";

class Advertising extends Component {

    state = {
        rate: [
            {
                title: " ",
                price: "25000",
                text: "     \" \"  1 .  \n" +
                    "        .  5\n" +
                    "     10.",
                type: "max"
            },
            {title: " ", price: "20000", text: "- ,         ", type: ""},
            {title: " ", price: "15000", text: "- ,         ", type: ""},
            {title: " ", price: "10000", text: "- ,         ", type: ""},
        ]
    };

    componentDidMount() {
        this.props.changeCurrentPage("different");
    }

    render() {
        const {rate} = this.state;
        return (
            <section>
                <h3 className="title_h3 title_pages"></h3>
                <div className="adv_container">
                    <p>    ,
                              
                    </p>
                    {rate.map((el, i) => {
                        const {title, price, text} = el;
                        return (
                            <aside className="adv_type" key={i}>
                                <div className="adv_name">
                                    <h4 className="title_h3">{title}</h4>
                                    <p>{price} </p>
                                </div>
                                <div className="adv_body">
                                    <p>{text}</p>
                                    <button className="blue_button"></button>
                                </div>
                            </aside>
                        )
                    })}
                </div>
            </section>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        changeCurrentPage: bindActionCreators(changeCurrentPage, dispatch),
    }
};

export default connect("", mapDispatchToProps)(Advertising);